            <div class="back" @click="back">
                <van-icon name="arrow-left" />
            </div>
            <van-search v-model="value"
             placeholder="请输入搜索关键词" 
             show-action shape="round" 
             @search="onSearch" 
             background="#f7f7f7">
                <div slot="action" @click="onSearch">搜索</div>
            </van-search>
            <!-- 下拉菜单 -->
            <van-dropdown-menu>
                <van-dropdown-item v-model="value1" :options="option1" />
                <van-dropdown-item v-model="value2" :options="option2" />
                <van-dropdown-item v-model="value3" :options="option3" />
                <van-dropdown-item v-model="value4" :options="option4" />
            </van-dropdown-menu>
            <!-- 提示框 -->
            <div class="search_filter_tab2" >
                <ul class="search_filter_tab2_ul">
                    <li>
                        <span>自营</span>

                    </li>
                    <li>
                        <span>促销</span>

                    </li>
                    <li>
                        <span>品牌</span>

                    </li>
                    <li @click="back2()">
                        <span>返回</span>

                    </li>

                </ul>

            </div>

        </div>
        <!-- 列表页 -->
        <div style="margin-top: 137px;">

            <div class="bottom_list" v-for="(item,index) in list" :key="index" @click="jump(index)" >
                <img :src="item.BigPic" alt="" class="pro_img">
                <div class="pro_na" v-text="item.title">
                    <img src="data:image/png;base64," alt="" class="pro_sell_type_icon"> 
                </div>
                <div class="pro_type">
                    <img src="data:image/png;base64," alt="">

                </div>
                <div class="pro_promotion_type">
                    <img src="data:image/png;base64," alt="">
                </div>
                <span class="review_num" >
                    {{item.pinlun+'条评论'}} &nbsp;&nbsp;
                    <i>{{'好评率'+item.haopin+'%'}}</i>

                </span>
                <div class="search_price">
                    <span class="price">
                        ￥
                        <i v-text="item.price"></i>
                        

                    </span>

                </div>

            </div>
           

        </div>

    </div>
</template>
<script>
export default {
  data() {
    return {
      value: "",
      value1: 0,
      value2: 0,
      value3: 0,
      value4: 0,
      list:[],
      option1: [
        {
          text: "全部类型",
          value: 0
        },
        {
          text: "热门类目",
          value: 1
        },
        {
          text: "心脑用药",
          value: 2
        },
        {
          text: "内分泌用药",
          value: 3
        },
        {
          text: "男科用药",
          value: 4
        },
        {
          text: "神经用药",
          value: 5
        }
      ],
      option2: [
        {
          text: "综合排序",
          value: 0
        },
        {
          text: "销量排序",
          value: 1
        },
        {
          text: "心脑用药",
          value: 2
        },
        {
          text: "好评排序",
          value: 3
        },
        {
          text: "价格从低到高",
          value: 4
        },
        {
          text: "价格从高到低",
          value: 5
        },
        {
          text: "最新上架",
          value: 6
        }
      ],
      option3: [
        {
          text: "列表",
          value: 0
        },
        {
          text: "大图",
          value: 1
        }
      ],
      option4: [
        {
          text: "筛选",
          value: 0
        }
      ]
    };
  },
  methods: {
    back() {
      this.$router.go(-1); //返回上一层
    },
    back2(){
      this.$router.push('home');
    },
    onSearch(){
        
    },
    // 路由跳转并传递ID
    jump(index){
      this.$router.push({path: 'detail',
      name:'detail',
       params:{
        _id :index
      }
      })
    }
  },
  // 获取数据
  async created() {
    let list = await this.$axios(
      "https://www.easy-mock.com/mock/5d403a1abf069f5f10cf34f1/example/1yaolist"
    );
    this.list = list.data.data

    // console.log(list.data.data);
  }
};
